async function  bindleftCommonSearchdropdown(instance, moduleName, filtername,data,AllCount) {
    var vm = instance;
var param = "moduleName='"+moduleName+"'&type="+filtername+""
var param1 = "moduleName=Ticketing&type="+filtername+""
if (filtername == 'USERS') {
    await DataServices.GetDDLData(param1).then((response) => {      
        if(response.data != '') {      
        if(response.data.DATA.length){
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[2].listOptions.push(obj);
            });
        }  }          
    });
}
else if(filtername=='Work_Type_List'){ 
    await DataServices.GetDDLData(param1).then((response) => {
        if(response.data != '') {
            if(response.data.DATA.length){
                response.data.DATA.forEach(element => {
                    let obj = {
                        name: element.name,
                        value: element.value
                    }                        
                    vm.leftSearchFields[3].listOptions.push(obj);
                });
            }
        }
    });
}
}
export default { buttons, Headers, leftsearchSchema, listActions , bindleftCommonSearchdropdown};