<script>
import DataServices from "@/services/DataService";

export default {
    props: {
        ServiceDetailRecord: {
            type: Array,
            required: true
        },
    },
    data() {
        return {
            ServiceDetailData: [],
            ServiceTypeName : null,
            isLoading: false,
            NoRecord: false,
            UserType: null
        }
    },
    created: function () {
        debugger
        var vm = this;
        if (!vm.isBlank(vm.ServiceDetailRecord)) {
            vm.ServiceDetailData = vm.ServiceDetailRecord;
            vm.ServiceTypeName = vm.ServiceDetailData[0].service_type_name;
            //vm.NoRecords = false;
        }
    },
    methods: {
        Close() {
            this.$parent.ServiceDetailPopup = false;
            vm.ServiceDetailData = null;
        },
    }
}
</script>
<style>
    .card-body {
    padding: 0;
}
.color-box {
    width: 60px;
    height: 18px;
    display: inline-block;
    border: 1px solid #ccc;
    vertical-align: middle;
    border-radius: 10px;
}
</style>
